.title {
    font-size: 55px;
    color: var(--dark);
    font-weight: bold;
    margin: 20px 0 0 0px;
    text-align: center;
}

.buttonsContainer {
    width: 230px;
    margin: 20px auto;
    padding-bottom: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.button {
    width: 100%;
    border: 1px var(--white) solid;
    border-radius: 10px;
    margin: 10px auto;
    padding: 10px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--white);
    background-color: var(--dark);
}

.button:hover {
    cursor: pointer;
    background-color: var(--orange);
}