:root {

    /**********Container*****************/
    /*Mobile*/
    --easyContainerWidthMobile: 280px;
    --mediumContainerWidthMobile: 280px;
    --hardContainerWidthMobile: 370px;
    /*Tablet*/
    --easyContainerWidthTablet: 500px;
    --mediumContainerWidthTablet: 560px;
    --hardContainerWidthTablet: 595px;
    /*Desktop*/
    --easyContainerWidthDesktop: 500px;
    --mediumContainerWidthDesktop: 518px;
    --hardContainerWidthDesktop: 540px;
    /***************************/

    /********************* Item **************/
    /*Desktop*/
    --easyLengthDesktop: 70px;
    --mediumLengthDesktop: 50px;
    --hardLengthDesktop: 45px;

    /*Tablet*/
    --easyLengthTablet: 70px;
    --mediumLengthTablet: 60px;
    --hardLengthTablet: 55px;

    /*Mobile*/
    --easyLengthMobile: 35px;
    --mediumLengthMobile: 30px;
    --hardLengthMobile: 32px;
    /***********************************/
}

.gameContainer {
    margin-top: 20px;
}

.container {
    justify-content: center
}

.helperPow {
    background: var(--bg);
}

.helperRest {
    color: var(--bg);
}

.helperRest:hover {
    cursor: pointer;
    color: var(--choosing);
    transition: color 0.3s ease-in-out 2s;
}

.header {
    font-size: 20px;
    text-align: center;
}

.header .btn {
    margin: 5px;
    font-size: 20px;
    color: var(--white);
    border: none;
}

.btn-danger,
.btn-danger:hover {
    background-color: var(--pink);
    cursor: inherit;
}

.btn-success,
.btn-success:hover {
    background-color: var(--green);
    cursor: inherit;
}

.btn-info,
.btn-info:hover {
    background-color: var(--blue);
}

.inactiveItem {
    background-color: var(--disks);
}

.activeItem,
.btn-info {
    background-color: var(--choosed);
}

.resultItemFalse {
    background: var(--pink);
}

.resultItemTrue {
    background: var(--green);
}


.item:hover {
    cursor: pointer;
    background-color: var(--choosing);
}

/* Mobile Styles */
@media only screen and (max-width: 400px) {
    .container {
        margin: 0px auto;
        display: flex;
        flex-wrap: wrap;
    }

    .easyContainer {
        width: var(--easyContainerWidthMobile)
    }

    .mediumContainer {
        width: var(--mediumContainerWidthMobile)
    }

    .hardContainer {
        width: var(--hardContainerWidthMobile)
    }

    .item {
        display: flex;
        color: var(--white);
        align-items: center;
        justify-content: center;
    }

    .hardItem {
        width: var(--hardLengthMobile);
        height: var(--hardLengthMobile);
        border-radius: var(--hardLengthMobile);
        margin: 2px;
    }

    .easyItem {
        width: var(--easyLengthMobile);
        height: var(--easyLengthMobile);
        border-radius: var(--easyLengthMobile);
        margin: 5px;
    }

    .mediumItem {
        width: var(--mediumLengthMobile);
        height: var(--mediumLengthMobile);
        border-radius: var(--mediumLengthMobile);
        margin: 2px;
    }

}

/* Tablet Styles */
@media only screen and (min-width: 401px) {

    .container {
        margin: 0px auto;
        display: flex;
        flex-wrap: wrap;
    }

    .easyContainer {
        min-width: var(--easyContainerWidthTablet)
    }

    .mediumContainer {
        min-width: var(--mediumContainerWidthTablet)
    }

    .hardContainer {
        min-width: var(--hardContainerWidthTablet)
    }

    .item {
        display: flex;
        color: var(--white);
        align-items: center;
        justify-content: center;
    }

    .hardItem {
        width: var(--hardLengthTablet);
        height: var(--hardLengthTablet);
        border-radius: var(--hardLengthTablet);
        margin: 3px;
    }

    .easyItem {
        width: var(--easyLengthTablet);
        height: var(--easyLengthTablet);
        border-radius: var(--easyLengthTablet);
        margin: 5px;
    }

    .mediumItem {
        width: var(--mediumLengthTablet);
        height: var(--mediumLengthTablet);
        border-radius: var(--mediumLengthTablet);
        margin: 5px;
    }
}


/* Desktop Styles */
@media only screen and (min-width: 961px) {
    .container {
        margin: 0px auto;
        display: flex;
        flex-wrap: wrap;
    }

    .easyContainer {
        min-width: var(--easyContainerWidthDesktop)
    }

    .mediumContainer {
        min-width: var(--mediumContainerWidthDesktop)
    }

    .hardContainer {
        min-width: var(--hardContainerWidthDesktop)
    }

    .item {
        display: flex;
        color: var(--white);
        align-items: center;
        justify-content: center;
    }

    .hardItem {
        width: var(--hardLengthDesktop);
        height: var(--hardLengthDesktop);
        border-radius: var(--hardLengthDesktop);
        margin: 5px;
    }

    .easyItem {
        width: var(--easyLengthDesktop);
        height: var(--easyLengthDesktop);
        border-radius: var(--easyLengthDesktop);
        margin: 8px;
    }

    .mediumItem {
        width: var(--mediumLengthDesktop);
        height: var(--mediumLengthDesktop);
        border-radius: var(--mediumLengthDesktop);
        margin: 7px;
    }

    .noneItem:hover {
        cursor: default;
        background-color: transparent;
    }
}