:root {
  --bg: #eaffcb;
  --disks: #caeb71;
  --blue: #3d93b4;
  --orange: #f35525;
  --choosed: #26605d;
  --choosing: #26605d;
  --dark: #395460;
  --green: #32a85b;
  --white: #fff;
  --black: #091119;
  --pink: #c44d7a;
}

.App {
  text-align: center;
  user-select: none;
  background-color: var(--bg);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

body {
  background-color: var(--dark);
}